import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { typeOfTraining } from 'dump/data';

const columns = [
  {
    title: 'Mã hệ',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Tên hệ',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Tên tiếng anh',
    dataIndex: 'nameEn',
    key: 'nameEn',
  },
  {
    title: 'Quy chế',
    dataIndex: 'regulation',
    key: 'regulation',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      typeOfTraining.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#system-training/form/edit/${record.code}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ListTeacherPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Hệ thống đào tạo">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/system-training/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={typeOfTraining} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTeacherPage />
  </Page>
);



